* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
  min-height: 100vh;
  min-width: 100vw;
  width: 100%;
  height: auto;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  position: relative;
}

p {
  color: #fff;
  font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1440 - 320)));
  font-weight: 500;
}


h2 {
  color: #fff;
  margin-bottom: calc(18px + (20 - 18) * ((100vw - 320px) / (1440 - 320)));
  margin-top: calc(30px + (40 - 30) * ((100vw - 320px) / (1440 - 320)));
  font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1440 - 320)));

}

.app {
  background-image: url('./assets/sunny-bg.jpg');
  background-size: cover;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-attachment: fixed;
  transition: 0.4s ease-out;
}

.app.warm {
  background-image: url('./assets/cloudy-bg.jpg');
  transition: 0.4s ease-out;
}


.info {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  bottom: 150px;
}

.info__degree {
  margin-left: calc(20px + (120 - 20) * ((100vw - 320px) / (1440 - 320)));
  /* margin-left: 12rem; */
  font-size: calc(100px + (160 - 100) * ((100vw - 320px) / (1440 - 320)));
}

.info__degree::after{
  content: "\00b0";
  margin-right: 5rem;
}

.info__right {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 40rem;
}

.info__city {
  font-size: calc(40px + (60 - 40) * ((100vw - 320px) / (1440 - 320)));
  margin-right: calc(0 + (50 - 0) * ((100vw - 320px) / (1440 - 320)));
  padding-right: calc(0 + (100 - 0) * ((100vw - 320px) / (1440 - 320)));
}

.info__time {
  font-size: calc(15px + (20 - 15) * ((100vw - 320px) / (1440 - 320)));
  margin-right: calc(0 + (80 - 0) * ((100vw - 320px) / (1440 - 320)));
}

.info__condition {
  font-size: calc(15px + (20 - 15) * ((100vw - 320px) / (1440 - 320)));
}

.info__logo{
  transition: 0.4s ease-out;
  width: 64px;
  height: 64px;
  margin-top: 2rem;
}

.info__row {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.search-box {
  width: 100%
  
}

.mg {
  padding-top: 2rem;
}

.right-box {
  position: absolute;
  right: 0;
  background: rgba(0, 0, 0, 0.5);
  height: 100vh;
  margin: 0;
  padding: calc(20px + (50 - 20) * ((100vw - 320px) / (1440 - 320)));
  width: 30%;
}

.search-box {
  width: 100%;
  margin: 20px auto
}

::placeholder {
  color: lightgray;
  opacity: 0.8;
  font-size: calc(12px + (16 - 12) * ((100vw - 320px) / (1440 - 320)));
}

.search-box .search-bar {
  display: block;
  width: 100%;

  appearance: none;
  outline: none;
  border: none;
  background: none;

  color: #fff;
  font-size: calc(15px + (20 - 15) * ((100vw - 320px) / (1440 - 320)));
  transition: 0.4s ease;
}

.search-box::after {
  content: "";
  border-bottom: solid 1px #fff;
  left: 20px;
  position: absolute;
  width: 87%;
  margin: 8px;
}

.list-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: calc(8px + (20 - 8) * ((100vw - 320px) / (1440 - 320)));
}


.text {
  color: lightgray;
}

@media screen and (max-width: 768px) {
  .app {
    height: 100vh;
    width: 100vw;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-attachment: scroll;
    transition: 0.4s ease-out;
  }

  .info {
    display: flex;
    flex-flow: nowrap column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    top: 0;
    left: center;
    /* margin-bottom: 1rem; */
  }

  .info__right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
  }

  .right-box {
    position: absolute;
    bottom: 0;
    height: 30vh;
    background: rgba(0, 0, 0, 0.5);
    margin: 0;
    padding-top: 10px;
    width: 100%;
  }

  .info__degree {
    font-size: 12rem;
    text-align: center;
    margin-left: 2rem;
  }

  .info__degree::after{
    margin-right: 0;
  }

  .info__city {
    font-size: 4rem;
    margin: 2rem 0;
    padding: 0;
    text-align: center;
  }

  .info__row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  
.info__time {
  font-size: 1.6rem;
  margin-right: 0;
  text-align: center;
}

}

@media screen and (max-width: 500px) {
  .app {
    height: 100vh;
    width: 100vw;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-attachment: scroll;
    transition: 0.4s ease-out;
  }
  .info {
    display: flex;
    flex-flow: nowrap column;
    justify-content: flex-start;
    align-items: center;
    margin: 0 auto;
    top: 0;
  }

  .info__right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
  }

  .right-box {
    position: absolute;
    bottom: 0;
    height: 40vh;
    background: rgba(0, 0, 0, 0.5);
    margin: 0;
    padding-top: 10px;
    width: 100vw;
  }

  .info__degree {
    text-align: center;
    margin: 1rem auto;
  }

  .info__degree::after{
    margin-right: 0;
  }

  .info__city {
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
  }

  .info__row {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  
.info__time {
  font-size: 1.6rem;
  margin-right: 0;
  text-align: center;
}

}

